<template>
	<div class="order-confirm">
		<div class="mdui-bottom-nav-fixed" v-show="!addAddress">
			<header-top :kfshow.sync="kfshow">
				<img src="../../assets/image/detail/icon-back.png" slot="menu" height="40" @click="goBack"
					class="go-back">
				<div class="mdui-center mdui-valign point-progress" slot="title">
					<ul>
						<li class="active">
							<i>1</i>
						</li>
						<li class="active"></li>
						<li class="active">
							<i>2</i>
						</li>
						<li></li>
						<li>
							<i>3</i>
						</li>
					</ul>
					<ul>
						<li class="active">
							<i>{{lang('')}}</i>
						</li>
						<li class="active">
							<i>{{lang('')}}</i>
						</li>
						<li class="active">
							<i>{{lang('')}}</i>
						</li>
					</ul>
				</div>
				<img
						width="28"
						height="28"
						src="../../assets/image/logo.png"
						alt="logo"
						class="logo"
						slot="home"
						@click="kfShow()"
				>
			</header-top>
			<!---->
			<div class="mdui-address card" @click="addAddress=true">
				<div class="click-add-address" v-if="!clientinfoList" style="margin-top: 10px">
					<button class="mdui-btn">+ {{lang('')}}</button>
				</div>
				<div class="click-add-address clearfix" v-else>
					<div style="display: inline-block;width: 70%;float: left;text-align: left">
						<span class="name">{{clientinfo.clientname}}</span>
						<span class="tel">{{clientinfo.clientphone}}</span>
						<div style="font-size: 12px;margin-top: 5px;">
							<span v-if="!clientinfo.store_delivery">[ {{lang('')}}]</span>
							<span v-else-if="clientinfo.store_delivery === '711'">[711]</span>
							<span v-else-if="clientinfo.store_delivery === ''">[]</span>
							{{clientinfo.city}} {{clientinfo.area}} {{clientinfo.store_delivery === ''||clientinfo.store_delivery === '711'?clientinfo.clientzipcode:clientinfo.clientaddress}}
						</div>
					</div>
					<i class="mdui-icon material-icons mdui-text-color-grey-500 array-right">keyboard_arrow_right</i>
				</div>
			</div>
			<!---->
			<div class="mdui-cart-products-view-sml card" @click="detailedList=true">
				<div class="mdui-cart-products">
					<div class="mdui-list">
						<img
								v-for="(item,index) of cart"
								class="mdui-cart-product-sku-img"
								:key="index"
								:src="item.img?config.cdnbase+item.img:''"
						>
					</div>
				</div>
				<span>
          {{lang('')}}{{allprice.allnum}}{{lang('')}}
          <i class="mdui-icon material-icons mdui-text-color-grey-500 array-right"
             style="margin-right: -10px;"
          >keyboard_arrow_right</i>
        </span>
			</div>
			<!---->
			<div class="mdui-color-white mdui-pannel mdui-pay-info card">
				<div class="openSelect openPayType">
					<div>
						<span class="title">{{lang('')}}</span>
					</div>
					<ul>
						<li v-show="config && config.ordercode.stripe_pk">
							<label class="mdui-radio">
								<input
										type="radio"
										name="group1"
										ref="type2"
										value="2"
										v-model="clientinfo.payment"
								>
								<i class="mdui-radio-icon"></i>
							</label>
							<div class="typeCon">
								<p>{{lang('')}}</p>
								<img src="../../assets/image/visa.png">
								<img src="../../assets/image/MasterCard.png">
								<img src="../../assets/image/American-Express.png">
								<img src="../../assets/image/discover.png">
							</div>
						</li>
						<li>
							<label class="mdui-radio">
								<input
										type="radio"
										name="group1"
										ref="type0"
										value="0"
										v-model="clientinfo.payment"
								>
								<i class="mdui-radio-icon"></i>
							</label>
							<div class="typeCon">
								<span>{{lang('')}}</span>
							</div>
						</li>
						<li v-show="siteInfo.Paypal && siteInfo.Paypal.paycode">
							<label class="mdui-radio">
								<input
										type="radio"
										name="group1"
										ref="type1"
										value="1"
										v-model="clientinfo.payment"
								>
								<i class="mdui-radio-icon"></i>
							</label>
							<div class="typeCon">
								<p>PayPal{{lang('')}}</p>
								<img class="paypal" src="../../assets/image/img-paypal.jpg">
							</div>
						</li>
					</ul>
				</div>
			</div>
			<!---->
			<!--<div class="mdui-color-white mdui-pannel mdui-pay-info card">
				<div class="openSelect couponDiscount">
					<div>
					<span class="title"></span>
					<div class="typeMes">
						<div></div>
						<i class="mdui-icon material-icons mdui-text-color-grey-500 array-right" style="margin-right: -10px;">keyboard_arrow_right</i>
					</div>
					</div>
				</div>
			</div>-->
			<!---->
			<div class="mdui-color-white mdui-pannel mdui-pay-info card">
				<ul class="mdui-list">
					<li class="mdui-list-item">
						<span class="orderProCount">{{lang('')}}{{allprice.allnum}}{{lang('')}}{{lang('')}}</span>
						<div class="orderPay">{{money}}{{allprice.allprice}}</div>
					</li>
					<li class="mdui-list-item" v-show="allprice.allprice<siteInfo.free_post_price">
						<span class="label">{{lang('')}}</span>
						<div class="orderExpressCount">{{money}}{{siteInfo.freight}}</div>
					</li>
					<li class="mdui-list-item" v-show="clientinfo.frieight">
						<span class="label">{{lang('')}}</span>
						<div class="orderExpressCount">{{money}}{{clientinfo.frieight}}</div>
					</li>
					<li class="mdui-list-item orderDiscountDepletion" v-if="false">
						<span class="label">{{lang('')}}</span>
						<div>- $13</div>
					</li>
				</ul>
			</div>
			<!---->
			<span
					class="open-comments"
					@click="addMessage=true"
					v-show="!addMessage&&clientinfo.clientotherinfo===''"
			>{{lang('')}}</span>
			<div
					class="mdui-cart-comments noWrite write-comments card"
					style="display: block;"
					v-show="addMessage"
			>
			<span>
				{{lang('')}}
				<img
					src="../../assets/image/icon-del.png"
					alt
					:style="{ 'opacity': clientinfo.clientotherinfo.length>0 ? 1 : 0.3 }"
					@click="clearText">
			</span>
				<textarea
						class="mdui-cart-comments-area"
						:placeholder="lang('...')"
						v-model="clientinfo.clientotherinfo"
				></textarea>
				<div class="comments-bottom">
					<button class="cancel" @click="addMessage=false">{{lang('')}}</button>
					<button
							class="submit"
							:style="{ 'opacity': clientinfo.clientotherinfo.length>0 ? 1 : 0.3 }"
							@click="finishText"
					>{{lang('')}}
					</button>
				</div>
			</div>
			<!---->
			<div
				class="mdui-cart-comments done-comments card"
				style="display: block;"
				v-show="clientinfo.clientotherinfo!==''&&!addMessage"
			>
        <span>
          {{lang('')}}
          <button class="edit" @click="addMessage=true">{{lang('')}}</button>
        </span>
				<div>{{clientinfo.clientotherinfo}}</div>
			</div>
			<!---->
			<div class="mdui-bottom-nav mdui-row mdui-color-white">
				<div class="info">
					<span>{{lang('')}}:</span>
					<span style="font-size:14px;">{{money}}</span>
					{{allprice.orderprice}}
					<span v-show="clientinfo.frieight">
					({{lang('')}}{{money}}{{clientinfo.frieight}})
				</span>
				</div>
				<button
						id="buyNow"
						class="mdui-btn mdui-btn-block mdui-color-red-900"
						:class="disableFlag?'disabledbtn':''"
						@click="go_buy()"
						:disabled="disableFlag"
				>{{lang('')}}
				</button>
			</div>
			<!---->
			<div class="product-list view-bottom" v-show="detailedList">
				<div class="view-bottom-box view-bottom-box-min view-move-in">
					<div class="view-bottom-head">
						<button class="success" style="opacity: 0;">{{lang('')}}</button>
						<span>{{lang('')}}</span>
						<button class="close">
							<img
									src="../../assets/image/icon-close2.png"
									class="close-pub"
									@click="detailedList=false"
							>
						</button>
					</div>
					<div class="view-bottom-content havfooter">
						<div class="mdui-cart-products-container">
							<div class="mdui-cart-products">
								<ul class="mdui-list">
									<li class="mdui-list-item" v-for="(item,index) of cart"
										:key="index">
										<router-link
												:to="`/detail/${item.tid}/${item.classgoods_id}`"
												class="mdui-list-item mdui-clearfix" style="margin-top: 0">
											<img class="mdui-cart-product-sku-img" :src="item.img?config.cdnbase+item.img:''">
											<div class="mdui-cart-product-sku-info ">
												<div class="mdui-list-item-title mdui-list-item-one-line">
													<div class="mdui-list-item-promotion-tags-depletion">
														<span>{{lang('')}}</span>
													</div>
													{{item.name}}
												</div>
												<div class="mdui-list-item-text">{{item.specname}} {{item.option1}} {{item.option2}}</div>
												<div class="mdui-list-item-text2">
													<span class="mdui-float-left mdui-list-item-text-price">
														{{money}}{{item.price}}
														<span class="original-price">{{money}}{{item.oldprice}}</span>
													</span>
													<span class="mdui-float-right promotion-num">x {{item.number}}</span>
												</div>
											</div>
										</router-link>
									</li>
								</ul>
							</div>
						</div>
					</div>
					<div class="view-bottom-footer">
						<router-link
								tag="button"
								class="sure"
								to="/home"
								@click="detailedList=false"
						>{{lang('')}}
						</router-link>
					</div>
				</div>
			</div>
		</div>
		<fill-address
				v-show="addAddress"
				@closeAddress="addAddress=false"
				:clientinfo.sync="clientinfo"
				:clientinfoList.sync="clientinfoList"
				@clientinfoList="clientinfoList=true"
		></fill-address>
		<div v-show="openCredit">
			<credit-card
					v-if="creditCard"
					@close="creditCard=false"
					:client-info="clientinfo"
					@goBuy="go_buy"
					@submission="submission=true"
					@nosubmission="submission=false"
					@openCredit="openCredit=false"
					ref="credit"
					@closeLoading='closeLoading'
			/>
		</div>
		<div class="marsk" ref="marsk" style="display: none"></div>
		<div style="display: none;" ref="paypal"></div>
	</div>
</template>

<script>
  import fillAddress from "./Address.vue";
  import {order} from "../../lib/goods";
  import CreditCard from "./CreditCard";
  import {mapMutations, mapState} from 'vuex';
  import {creditPlgin} from "../../lib/util";
  export default {
    data() {
      return {
        kfshow: false,
        detailedList: false, // 
        addAddress: false, // 
        addMessage: false, // 
        clientinfoList: false, // 
        cart: [],
        money: '',
        clientinfo: {
          payment: "0"
        },
        creditCard: false,
        submission: false,
        openCredit: false,
        disableFlag: false,
		successUrl: '',
		orderUrl: ''
      };
    },
    created() {
      this.filtration_cart(); // 
      this.get_clientinfo(); // 
      this.money = this.siteInfo.money
		this.currency = 'TWD'
		if (this.siteInfo.money === 'NT') {
			this.currency = 'TWD'
		} else if (this.siteInfo.money === 'HK') {
			this.currency = 'HKD'
		} else if (this.siteInfo.money === 'RM') {
			this.currency = 'MYR'
		} else if (this.siteInfo.money === '') {
			this.currency = 'THB'
		} else if (this.siteInfo.money === 'Rp') {
			this.currency = 'INR'
		} else if (this.siteInfo.money === '') {
			this.currency = 'JPY'
		}
    },
    methods: {
      ...mapMutations(['setOrder','setImgs']),
      kfShow() {
        this.kfshow = !this.kfshow;
      },
      finishText() {
        if (this.clientinfo.clientotherinfo.length > 0) {
          // 
          this.addMessage = false;
        } else {
          return false;
        }
      },
      clearText() {
        this.clientinfo.clientotherinfo = "";
      },
      get_clientinfo() {
        let clientinfo = localStorage.getItem(this.siteInfo.money + "_clientinfo");
        if (clientinfo) {
          this.clientinfo = JSON.parse(clientinfo);
          this.clientinfoList = true;
        } else {
          this.clientinfo = {
            clientname: "",
            clientphone: "",
            clientaddress: "",
            clientemail: "",
            clientotherinfo: "",
            city: "",
            area: "",
            store: "",
            store_delivery: "",
            payment: "0",
            token: "",
			currency: '',
			cancleUrl: ''
          };
        }
      },
      // 
      filtration_cart() {
        let cart = JSON.parse(localStorage.getItem("cart_info"));
        if (cart && cart.list.length > 0) {
          cart = JSON.parse(localStorage.getItem("cart_info"));
          //          this.money = cart.money
          cart.list.forEach(item => {
            if (item.checked) {
              this.cart.push(item);
            }
          });
          if (this.cart.length <= 0) {
            this.$router.push("/shoppingCart");
          }
        } else {
          this.cart = [];
          this.$router.push("/home");
        }
      },
      goBack() {
				if(window.history.length <= 1){
					this.$router.push({path:'/'})
				}else{
					window.history.go(-1)
				}
      },
      checkout() {
        if (this.clientinfoList) {
			/*  */
          if (this.cart.length === 0) return this.$toast.bottom("");
          let name = /^\s*$/;
          if (
            !this.clientinfo.clientname ||
            name.test(this.clientinfo.clientname)
          ) {
            return this.$toast.bottom("", {width: '170px'});
          }
        } else {
          return this.$toast.bottom("");
        }
      },
      closeLoading () {
        this.openCredit = true;
        this.$refs.marsk.style.display = "none";
        this.$loading.close();
      },
		orderMethod: async function () {
			let ranges = [
				"\ud83c[\udf00-\udfff]",
				"\ud83d[\udc00-\ude4f]",
				"\ud83d[\ude80-\udeff]"
			];
			this.clientinfo.clientemail = this.clientinfo.clientemail
					? this.clientinfo.clientemail.replace(
							new RegExp(ranges.join("|"), "g"),
							""
					)
					: "";
			this.clientinfo.clientaddress = this.clientinfo.clientaddress.replace(
					new RegExp(ranges.join("|"), "g"),
					""
			);
			this.clientinfo.clientotherinfo = this.clientinfo.clientotherinfo
					? this.clientinfo.clientotherinfo.replace(
							new RegExp(ranges.join("|"), "g"),
							""
					)
					: "";
			this.$toast.bottom("", {width: '170px'});
			this.disableFlag = true;
			let goodsinfo = [];
			let cartinfo = [];
			this.cart.map(item => {
				if (item.checked) {
					goodsinfo.push({tid: item.tid, classgoodsid: item.classgoods_id});
					cartinfo.push([
						{
							sku: item.sku,
							number: item.number,
							priceflag: item.priceflag
						}
					]);
				}
			});
			let clientinfo = this.clientinfo;
			let referrer = document.referrer ? document.referrer : "";
			let siteurl = window.location.href;
			if (typeof window.fbq === "function") {
				var currency = 'TWD';
				if (this.siteInfo.money === 'RM')
					currency = 'MYR';
				if (this.siteInfo.money === 'HK')
					currency = 'HKD';
				else if (this.siteInfo.money === 'S$')
					currency = 'SGD';
				else if (this.siteInfo.money === '')
					currency = 'THB';
				else if (this.siteInfo.money === '')
					currency = 'JPN';
				window.fbq('track', 'Purchase', {
					value: this.allprice.allprice,
					currency: currency
				});
			}
			let data = await order.submit_order(
					goodsinfo,
					cartinfo,
					clientinfo,
					referrer,
					siteurl
			);
			if (data.Error) {
				let languageInfo = this.siteInfo.language;
				this.disableFlag = false;
				return this.$toast.bottom(data.Info[languageInfo]);
			} else {
				this.setOrder(data.orderInfo)
				let cartinfo = JSON.parse(localStorage.getItem("cart_info"));
				let imgs = [];
				for (var i = 0; i < cartinfo.list.length; i++) {
					if (cartinfo.list[i].checked) {
						// i
						imgs.push(cartinfo.list[i].img);
						if (this.clientinfo.payment !== '1') {
							cartinfo.list.splice(i--, 1);
						}
					}
				}
				this.setImgs(imgs)
				this.$store.state.orderId = data.orderInfo.orderid;
				if (data.orderInfo.pay_receipt_url) {
					this.$store.state.pay_receipt_url = data.orderInfo.pay_receipt_url;
				}
				localStorage.setItem("cart_info", JSON.stringify(cartinfo));
				this.disableFlag = false;
				if (this.clientinfo.payment==='1') {
					let paycode = this.siteInfo.Paypal.paycode
					let sitename = this.siteInfo.sitename
					let orderid = data.orderInfo.orderid
					let price = data.orderInfo.orderprice
					this.$refs.paypal.innerHTML = `<form action="https://www.paypal.com/cgi-bin/webscr" method="post" id="sform">
							  <input type="hidden" name="cmd" value="_xclick">
							  <input type="hidden" name="business" value="${paycode}">
							  <input type="hidden" name="item_name" value="${sitename}">
							  <input id='item_number' type="hidden" name="item_number" value="1">
							  <input id='amount' type="hidden" name="amount" value="${price}">  <!--   -->
							  <input type="hidden" name="currency_code" value="${this.currency}">
							  <input type='hidden' name='return' value='${this.successUrl}'>
							  <input type='hidden' name='notify_url' value='${this.orderUrl}'>
							  <input type='hidden' name='cancel_return' value='${this.cancleUrl}'>
							  <input id='invoice' type='hidden' name='invoice' value='${orderid}'> <!--   -->
							  <input type='hidden' name='charset' value='utf-8'><!--  -->
							  <input type="hidden" name="no_shipping" value="1"> <!--  -->
							  <input type="hidden" name="no_note" value="remark Recharge">
							  <input type="hidden" name="bn" value="IC_Sample">
							  <input type='hidden' name='rm' value='2'>
							  <input type="image" src='https://shop.static.zxkf.cc/paypal.svg' name="submit" alt="payments with payPal!" class="paypal-img"></form>`
					document.getElementsByClassName('paypal-img')[0].click()
				} else {
					this.$router.push('/finishorder')
				}
			}
		},
		go_buy: async function () {
			if (this.$refs.type0.checked && !this.submission || this.$refs.type1.checked && !this.submission) {
				this.checkout();
				this.orderMethod();
			}
			if (this.$refs.type2.checked && !this.submission) {
				if (this.clientinfoList) {
					/*  */
					if (this.cart.length === 0) return this.$toast.bottom("");
					let name = /^\s*$/;
					if (
							!this.clientinfo.clientname ||
							name.test(this.clientinfo.clientname)
					) {
						return this.$toast.bottom("", {width: '170px'});
					}
				} else {
					return this.$toast.bottom("");
				}
				var self = this;
				self.creditCard = true;
				self.$refs.marsk.style.display = "block";
				self.$loading("loading...");
			}
			if (this.$refs.type2.checked && this.submission) {
				if (!sessionStorage.getItem("token")) {
					return false;
				} else {
					this.checkout();
					this.clientinfo.token = sessionStorage.getItem("token");
					/*  */
					let ranges = [
						"\ud83c[\udf00-\udfff]",
						"\ud83d[\udc00-\ude4f]",
						"\ud83d[\ude80-\udeff]"
					];
					this.clientinfo.clientemail = this.clientinfo.clientemail
							? this.clientinfo.clientemail.replace(
									new RegExp(ranges.join("|"), "g"),
									""
							)
							: "";
					this.clientinfo.clientaddress = this.clientinfo.clientaddress.replace(
							new RegExp(ranges.join("|"), "g"),
							""
					);
					this.clientinfo.clientotherinfo = this.clientinfo.clientotherinfo
							? this.clientinfo.clientotherinfo.replace(
									new RegExp(ranges.join("|"), "g"),
									""
							)
							: "";
					this.$toast.bottom("", {width: '170px'});
					let goodsinfo = [];
					let cartinfo = [];
					this.cart.map(item => {
						if (item.checked) {
							goodsinfo.push({tid: item.tid, classgoodsid: item.classgoods_id});
							cartinfo.push([
								{
									sku: item.sku,
									number: item.number,
									priceflag: item.priceflag
								}
							]);
						}
					});
					let clientinfo = this.clientinfo;
					let referrer = document.referrer ? document.referrer : "";
					let siteurl = window.location.href;
					if (typeof window.fbq === "function") {
						var currency = 'TWD';
						if (this.siteInfo.money === 'RM')
							currency = 'MYR';
						if (this.siteInfo.money === 'HK')
							currency = 'HKD';
						else if (this.siteInfo.money === 'S$')
							currency = 'SGD';
						else if (this.siteInfo.money === '')
							currency = 'THB';
						else if (this.siteInfo.money === '')
							currency = 'JPN';
						window.fbq('track', 'Purchase', {
							value: this.allprice.allprice,
							currency: currency
						})
					}
					let data = await order.submit_order(
							goodsinfo,
							cartinfo,
							clientinfo,
							referrer,
							siteurl
					);
					if (data.Error) {
						let languageInfo = this.siteInfo.language;
						this.$refs.credit.closeMarsk()
						return this.$toast.bottom(data.Info[languageInfo]);
					} else {
						this.setOrder(data.orderInfo)
						this.$refs.credit.closeMarsk()
						let cartinfo = JSON.parse(localStorage.getItem("cart_info"));
						for (var i = 0; i < cartinfo.list.length; i++) {
							if (cartinfo.list[i].checked) {
								// i
								this.$store.state.imglist.push(cartinfo.list[i].img);
								cartinfo.list.splice(i--, 1);
							}
						}
						this.$store.state.orderId = data.orderInfo.orderid;
						if (data.orderInfo.pay_receipt_url) {
							this.$store.state.pay_receipt_url = data.orderInfo.pay_receipt_url;
						}
						localStorage.setItem("cart_info", JSON.stringify(cartinfo));
						this.$router.push("/finishOrder");
					}
				}
				this.submission = false;
				setTimeout(() => {
					this.closeLoading()
				}, 20000);
			}
		}
		//
    },
    components: {
      fillAddress,
      CreditCard
    },
    computed: {
      ...mapState(["siteInfo", "language", "config"]),
      lang() {
        return function (str) {
          return this.language ? this.language[str] : "";
        };
      },
      allprice() {
        let allprice = 0;
        let allnum = 0;
        let orderprice = 0;

        if (this.cart) {
          for (let item of this.cart) {
            if (item.checked) {
              allprice += item.number * item.price;
              orderprice = allprice;
              allnum += item.number;
            }
          }
          if (this.siteInfo.free_post_price > allprice) {
            orderprice = allprice + this.siteInfo.freight;
          }
          if (this.clientinfo.frieight) {
            orderprice = orderprice + this.clientinfo.frieight;
          }
        }
        return {allprice: allprice, allnum: allnum, orderprice: orderprice};
      }
    },
    mounted() {
		this.orderUrl = `${window.location.origin}/api/shop/paypalPayment`
		this.successUrl = `${window.location.origin}/success`
		this.cancleUrl = `${window.location.origin}/finishOrder`
      creditPlgin({
        id: "spt",
        ele: "script",
        src: "https://js.stripe.com/v3/"
      });
    }
  };
</script>

