<template>
	<div class="credit-card">
		<div class="card-info">
			<div class="card-head">
				<button class="close" @click="close">
					<img src="../../assets/image/icon-close2.png">
				</button>
				<span>{{lang('')}}</span>
				<div></div>
			</div>
			<div class="card-content">
				<form action="/finishOrder" method="post" id="payment-form">
					<div class="form-row">
						<div id="card-element">
							<!-- A Stripe Element will be inserted here. -->
						</div>
						<!-- Used to display form errors. -->
						<div id="card-errors" role="alert"></div>
					</div>
					<button class="sure pay-with-stripe" :disabled="isSubmit" ref="subBtn">{{lang('')}}</button>
				</form>
			</div>
		</div>
		<div class="credit-marsk" ref="creditMarsk" v-show="isSubmit"></div>
	</div>
</template>

<script>
	/* eslint-disable no-undef */
    import { mapState } from "vuex";

    export default {
      data() {
        return {
          complete: false,
          stripeOptions: {
            // see https://stripe.com/docs/stripe.js#element-options for details
          },
          tip: "",
          isSubmit: false
        };
      },
      methods: {
			/* async pay () {
			let token = sessionStorage.getItem('token')
			let self = this
			self.clientInfo.payment = 2
			if (!token) {
			await createToken().then(function (data) {
			self.clientInfo.token = data.token.id
			sessionStorage.setItem('token', data.token.id)
			})
			} else {
			self.clientInfo.token = token
			}
			}, */
        close() {
          this.$emit("nosubmission");
          this.$emit("close");
          this.$emit('openCredit')
        },
        closeMarsk () {
          this.isSubmit = false;
          this.$loading.close();
          this.$refs.subBtn.style.background = "#b4272d";
        }
      },
      props: ["clientInfo"],
      mounted() {
        var ordercode = this.config.ordercode
        var self = this;
        var stripe = Stripe(ordercode.stripe_pk);
        var elements = stripe.elements();
        var style = {
          base: {
            color: "#32325d",
            fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
            fontSmoothing: "antialiased",
            fontSize: "16px",
            "::placeholder": {
              color: "#aab7c4"
            }
          },
          invalid: {
            color: "#fa755a",
            iconColor: "#fa755a"
          }
        };
        var card = elements.create("card", { style: style });
        card.mount("#card-element");
        card.addEventListener("change", function(event) {
          var displayError = document.getElementById("card-errors");
          if (event.error) {
            displayError.textContent = event.error.message;
            displayError.style.color = "red";
          } else {
            displayError.textContent = "";
          }
        });
        var form = document.getElementById("payment-form");
        form.addEventListener("submit", function(event) {
          self.$loading("loading...");
          self.isSubmit = true;
          event.preventDefault();
          stripe.createToken(card).then(function(result) {
            if (result.error) {
              self.closeMarsk()
              var errorElement = document.getElementById("card-errors");
              errorElement.textContent = result.error.message;
            } else {
              self.$refs.subBtn.style.background = "#ccc";
              sessionStorage.setItem('token', result.token.id)
              stripeTokenHandler(result.token);
            }
          });
        });
        function stripeTokenHandler(token) {
          var form = document.getElementById("payment-form");
          var hiddenInput = document.createElement("input");
          hiddenInput.setAttribute("type", "hidden");
          hiddenInput.setAttribute("name", "stripeToken");
          hiddenInput.setAttribute("value", token.id);
          form.appendChild(hiddenInput);
          self.$emit("submission");
          self.$emit("goBuy");
          setTimeout(() => {
            self.closeMarsk()
            self.$toast('')
          }, 20000);
        }
		/* setInterval(() => {
		console.log(document.getElementById('card-element').innerHTML)
		}, 50); */
		card.addEventListener('ready',function () {
          self.$emit('closeLoading')
        })
		/*setTimeout(() => {
			this.$emit('closeLoading')
		}, 2000);*/
      },
      computed: {
        ...mapState(["language", "siteInfo", 'config']),
        lang() {
          return function(str) {
            return this.language ? this.language[str] : "";
          };
        }
      }
    };
</script>

<style scoped lang="stylus">
	.credit-card
		background: rgba(0, 0, 0, 0.6)
		position: fixed;
		width: 100vw;
		top: 0;
		bottom: 0;
		z-index: 9999;
		max-width: 750px;
		box-sizing: border-box;
		.card-info
			top: calc(100% - 240px);
			animation: 0s moveIn_min ease-in-out;
			height: 440px;
			position: absolute;
			width: 100vw;
			max-width: 750px;
			background: #FFF;
			overflow: hidden;
			border-top-left-radius: 6px;
			border-top-right-radius: 6px;
			.card-head
				padding: 0 8px;
				display: flex;
				align-items: center;
				justify-content: space-between;
				text-align: center;
				font-size: 16px;
				line-height: 44px;
				height: 44px;
				color: #000;
				.close
					padding: 0 0 0 4px;
					border: none;
					background: transparent;
					img
						width: 28px;
						height: 28px;
			.card-content
				padding: 0 4%;
				height: calc(100% - 44px);
				overflow-y: auto;
				.card-number
					box-sizing: border-box;
					max-height: 40px;
					padding: 10px 12px;
					border: 1px solid transparent;
					border-radius: 4px;
					background-color: white;
					box-shadow: 0 1px 3px 0 #e6ebf1;
				.sure
					color: #fff;
					height: 40px;
					width: 100%;
					border-radius: 20px;
					background: #B4272D;
					border: 0;
					margin-top: 16px;
					font-size: 16px;
				.tip
					margin-top: 10px;
					color: #B4272D;
		.StripeElement {
			box-sizing: border-box;

			height: 40px;

			padding: 10px 12px;

			border: 1px solid transparent;
			border-radius: 4px;
			background-color: white;

			box-shadow: 0 1px 3px 0 #e6ebf1;
			-webkit-transition: box-shadow 150ms ease;
			transition: box-shadow 150ms ease;
		}
		.credit-marsk {
			background-color: rgba(0,0,0,0.6)
			position: absolute;
			width: 100vw;
			height 100%
			max-width: 750px;
			top: calc(100% - 240px);
		}

	.StripeElement--focus {
		box-shadow: 0 1px 3px 0 #cfd7df;
	}

	.StripeElement--invalid {
		border-color: #fa755a;
	}

	.StripeElement--webkit-autofill {
		background-color: #fefde5 !important;
	}
</style>
