<template>
  <div id="address_wrap">
	<div class="address-content">
		<ul class="mdui-list">
			<!---->
			<li class="mdui-list-item">
				<label for="clientname" class="text-left clientname">
					{{lang('')}}
				</label>
				<input type="text" class="necessary text-left" :placeholder="lang('')+lang('')" name="firstname" v-model="clientinfo.clientname" id="clientname">
			</li>
			<li class="mdui-divider mdui-m-y-0"></li>
			<!---->
			<li class="mdui-list-item">
				<label for="clientphone" class="text-left clientphone">{{lang('')}}</label>
				<input type="number" class="necessary text-left" :placeholder="lang('')+lang('')" name="telephone"
				v-model="clientinfo.clientphone" id="clientphone">
			</li>
			<li class="mdui-divider mdui-m-y-0"></li>
			<!---->
			<li class="mdui-list-item" v-show="!editClient">
				<label for="clientphone" class="text-left clientphone">{{lang('')}}</label>
				<div>
					{{clientinfo.city}}/{{clientinfo.area}}
				</div>
				<button class="mdui-btn mdui-color-theme-accentmdui-ripple mdui-float-right" @click="editAddress()"><i class="mdui-icon material-icons" style="font-size: 16px;color: #b4272d;">&#xe22b;</i></button>
			</li>
			<li class="mdui-list-item" id="provinceDiv" v-show="editClient">
				<label for="city" class="text-left province">{{lang('')}}</label>
				<div class="input-infor">
					<select class="select" id="city" v-model="city"
					@change="init_Select_City(money, city, '', 'area')">
						<option :value="item.city" v-for="(item,index) in cityData" :key="index">{{item.city}}</option>
					</select>
					<select class="select" id="area" v-model="area"
					@change="queryId()">
						<option :value="item.area" v-for="(item,index) in areaData" :key="index">{{item.area}}</option>
					</select>
				</div>
			</li>
			<!---->
			<li class="mdui-list-item">
				<div class="input-infor">
					<input type="text" class="necessary_address text-left" :placeholder="lang('')+lang('')" name="address1" v-model="clientinfo.clientaddress" id="clientaddress">
				</div>
			</li>
			<li class="mdui-divider mdui-m-y-0"></li>
			<!--email-->
			<li class="mdui-list-item">
				<label for="clientemail" class="text-left clientemail">Email:</label>
				<input type="text" class="necessary text-left" placeholder="E-Mail" name="email"
				v-model="clientinfo.clientemail" id="clientemail">
			</li>
			<li class="mdui-divider mdui-m-y-0"></li>
		</ul>
		<button class="success saveAddress" @click="address_btn()">{{lang('')}}</button>
	</div>
  </div>
</template>

<script>
  import postcode from '../../../lib/postcode'
  import {mapState} from 'vuex'

  export default {
    name: "addres",
    props: {
      clientinfo: {
        type: Object
      }
    },
    data() {
      return {
        money:'',
        cityData: [],
        areaData: [],
        area: '',
        city: '',
        editClient:true
      };
    },
    created(){
      if(this.clientinfo.city){
        this.editClient = false
      }
    },
    async mounted() {
      this.money = this.siteInfo.money;
      await this.get_address();
      this.init_Select_City(this.money, null, null, 'city')
    },
    methods: {
      async get_address(){
        await postcode.load(this.money)
      },
      editAddress(){
        this.editClient = true
        this.clientinfo.city = "";
        this.clientinfo.area = ""
      },
      //
      async init_Select_City(code, city, area, ele) {
        let address = postcode.get(code, city, area, ele);
        if (ele === 'city') {
          this.cityData = address;
        } else {
          if (ele === 'area') {
            this.areaData = address;
            this.clientinfo.city = this.city;
            this.clientinfo.area = ""
          } else {
            this.codeData = address;
            this.clientinfo.area = this.area
          }
        }
      },
      queryId(){
        this.clientinfo.area = this.area
        this.areaData.map((code) => {
          if(code.area === this.clientinfo.area){
            this.$set(this.clientinfo,'frieight',code.frieight);
          }
        })
      },
      address_btn(){
        if(!this.clientinfo.clientname){
          this.$toast.bottom(this.lang('...'))
          return
        }else if(!this.clientinfo.clientphone){
          this.$toast.bottom(this.lang('...'))
          return
        }else if(!this.clientinfo.city || !this.clientinfo.area || !this.clientinfo.clientaddress){
          this.$toast.bottom(this.lang('...'))
          return
        }
        this.$set(this.clientinfo,'money',this.money);
        this.editClient = false
        localStorage.setItem(this.money+'_clientinfo',JSON.stringify(this.clientinfo));
        this.$emit('closeAddress');
        this.$emit('clientinfoList');
      }
    },
    computed: {
      ...mapState(["siteInfo", "language"]),
      lang() {
        return function (str) {
          return this.language ? this.language[str] : "";
        };
      }
    }
  };
</script>
<style scoped>

</style>
