<template>
  <!---->
  <div id="address_wrap">
    <div class="address-content">
      <ul class="mdui-list">
        <!---->
        <li class="mdui-list-item">
          <label class="form-list text-left hdfk">
            <span class="name">{{lang('')}}</span>
          </label>
          <div class="input-infor">
            <span class="check">
              <label class="mdui-radio">
                <input
                  type="radio"
                  :checked="clientinfo.store_delivery?false:true"
                  name="wfpayment"
                  id="payment1"
                  @click="init_Select_City(money, city,'', 'city','click','');"
                  value="cod"
                  class="input-radio"
                >
                <i class="mdui-radio-icon"></i>
              </label>
            </span>
            <span class="check">
              <label class="mdui-radio">
                <input
                  type="radio"
                  name="wfpayment"
                  id="payment2"
                  @click="init_Select_City(money, city,'', 'city','click','711');"
                  value="711"
                  :checked="clientinfo.store_delivery === '711'?true:false"
                >
                <i class="mdui-radio-icon"></i>
                711
              </label>
            </span>
            <span class="check">
              <label class="mdui-radio">
                <input
                  type="radio"
                  name="wfpayment"
                  id="payment3"
                  @click="init_Select_City(money, city,'', 'city','click','');"
                  value="FamilyMart"
                  class="input-radio"
                  :checked="clientinfo.store_delivery === ''?true:false"
                >
                <i class="mdui-radio-icon"></i>
                
              </label>
            </span>
          </div>
        </li>
        <!---->
        <li class="mdui-list-item">
          <label for="clientname" class="text-left clientname">{{lang('')}}</label>
          <input
            type="text"
            class="necessary text-left"
            :placeholder="''"
            name="firstname"
            v-model="clientinfo.clientname"
            id="clientname"
          >
        </li>
        <li class="mdui-divider mdui-m-y-0"></li>
        <!---->
        <li class="mdui-list-item">
          <label for="clientphone" class="text-left clientphone">{{lang('')}} </label>
          <div class="input-infor">
            <input
              type="text"
              class="necessary text-left"
              :placeholder="''"
              name="telephone"
              v-model="clientinfo.clientphone"
              id="clientphone"
            >
          </div>
        </li>
        <li class="mdui-divider mdui-m-y-0"></li>
        <!---->
        <li class="mdui-list-item" v-show="!editClient">
           <label for="clientphone" class="text-left clientphone">{{lang('')}}</label>
           <div>
              {{clientinfo.city}}/{{clientinfo.area}} <span v-if="clientinfo.clientzipcode">/{{clientinfo.clientzipcode}}</span>
           </div>
           <button class="mdui-btn mdui-color-theme-accentmdui-ripple mdui-float-right" @click="editAddress()"><i class="mdui-icon material-icons" style="font-size: 16px;color: #b4272d;">&#xe22b;</i></button>
        </li>
        <li class="mdui-list-item" id="provinceDiv" v-show="editClient">
          <label for="city" class="text-left province">{{lang('')}}</label>
          <!---->
          <div class="input-infor">
            <select class="select" id="city" v-model="city"
                 @change="init_Select_City(money, city,'', 'area','',replace_shop)">
                <option :value="item.city" v-for="(item,index) in cityData" :key="index">{{item.city}}</option>
            </select>
            <select class="select" id="area" v-model="area"
                    @change="init_Select_City(money, city, area, 'code','',replace_shop)">
              <option :value="item.area" v-for="(item,index) in areaData" :key="index">{{item.area}}</option>
            </select>
          </div>
        </li>
        <!---->
        <li class="mdui-list-item">
          <div class="input-infor">
            <input
              v-show="replace_shop !== '711' && replace_shop !== ''"
              type="text"
              style="white-space:normal;word-wrap:break-word;word-break:break-all"
              class="necessary_address text-left"
              :placeholder="''"
              name="address1"
              v-model="clientinfo.clientaddress"
              id="clientaddress"
              :disabled="disabled_flag"
            >
            <select class="select" id="street" v-model="street"
                    @change="streetSele()"  v-show="(replace_shop === '711' || replace_shop === '') && editClient && codeFlag">
              <option :value="item.road" v-for="(item,index) in streetData" :key="index">{{item.road}}</option>
            </select>
            <select class="select" id="store" v-model="clientzipcode" :class="!codeFlag?'mag-left':''"
                    @change="queryId()"  v-show="(replace_shop === '711' || replace_shop === '') && editClient">
              <option :value="item.postcode" v-for="(item,index) in codeData" :key="index">{{item.postcode}}</option>
            </select>
          </div>
        </li>
        <li class="mdui-divider mdui-m-y-0"></li>
        <!--email-->
        <li class="mdui-list-item">
          <label for="clientemail" class="text-left clientemail">Email:</label>
          <div class="input-infor">
            <input
              type="text"
              class="necessary text-left"
              name="email"
              placeholder="(,)"
              v-model="clientinfo.clientemail"
              id="clientemail"
            >
          </div>
        </li>
        <li class="mdui-divider mdui-m-y-0"></li>
      </ul>
    </div>
    <button class="success saveAddress" @click="address_btn()">{{lang('')}}</button>
  </div>
</template>

<script>
import postcode from "../../../lib/postcode";
import {mapState} from 'vuex'
export default {
  name: "addres",
  props: {
    clientinfo: {
      type: Object
    }
  },
  data() {
    return {
      money: '',
      clientotherinfo: "",
      cityData: [],
      areaData: [],
      codeData: [],
      storeData: [],
      streetData: [],
      newList: [],
      area: '',
      city: '',
      street:'',
      clientzipcode: '',
      replace_shop: "",
      disabled_flag: false,
      editClient:true,
      codeFlag: true,
    };
  },
  created() {
    if(this.clientinfo.city){
      this.editClient = false
    }
  },
  async mounted() {
    this.money = this.siteInfo.money;
    await this.get_address();
    this.init_Select_City(this.money, null,null, 'city','',this.clientinfo.store_delivery)
  },
  methods: {
    async get_address(){
      await postcode.load(this.money)
    },
    editAddress(delivery){
      this.editClient = true
      this.clientinfo.city = "";
      this.clientinfo.area = ""
      this.clientinfo.clientaddress = ""
      this.clientinfo.store = "";
      this.clientinfo.store_delivery = delivery;
      this.clientinfo.clientzipcode = "";
      this.city = "";
      this.area = "";
      this.store = "";
      this.street = "";
      this.areaData = [];
      this.codeData = [];
      this.storeData = [];
      this.streetData = [];
      this.replace_shop = delivery;
      this.clientzipcode = "";
      this.init_Select_City(this.money, null,null, 'city','',delivery)
    },
    editAddress_cs(){
      this.editClient = true
      this.clientinfo.city = "";
      this.clientinfo.area = ""
      this.clientinfo.clientaddress = ""
      this.clientinfo.store = "";
      this.city = "";
      this.area = "";
      this.store = "";
    },
    //
    async init_Select_City(code, city, area, ele, event, delivery) {
      this.replace_shop = delivery;
      this.clientinfo.store_delivery = delivery;
      this.disabled_flag = false;
      if(event === 'click'){
        this.editAddress(delivery);
      }
      await postcode.load(this.money)
      let address = postcode.get(code, city, area, ele);
      if (ele === "city") {
        this.cityData = address;
      } else {
        if (ele === "area") {
          this.areaData = address;
          this.clientinfo.city = this.city;
          this.clientinfo.area = "";
          this.clientinfo.clientzipcode = "";
          this.clientzipcode = "";
          //this.clientinfo.store_delivery = "";
        } else {
          this.codeData=[];
          if(delivery === "711"){
            for (let i in address) {
              if(address[i].type+''==='2'){
                this.codeData.push(address[i]);
              }
            }
          }else if(delivery === ""){
            for (let i in address) {
              if(address[i].type+''==='1'){
                this.codeData.push(address[i]);
              }
            }
          }else{
            this.queryId();
          }
          if(this.codeData.length===0){
            if(delivery === "711"){
              this.codeData = [{postcode:',',type:2,road:","}];
            }else if(delivery === ""){
              this.codeData = [{postcode:',',type:1,road:","}];
            }
            this.codeFlag = false
            // this.codeData = [{postcode:',',type:2}];
            this.clientzipcode = ","
          }else{
            if(delivery === "711"){
              this.codeData.unshift({postcode:'',type:2,road:""});
            }else if(delivery === ""){
              this.codeData.unshift({postcode:'',type:1,road:""});
            }
            this.clientzipcode = "";
            this.street = ""
            this.codeFlag = true
          }
          this.clientinfo.area = this.area;
          this.clientinfo.clientzipcode = "";
          this.streetData = this.codeData;
          // this.clientinfo.store_delivery = "";
        }
      }
    },
    streetSele(){
      if(this.street !== ""){
        this.codeData = [];
        this.streetData.map((item) => {
          if(item.road === this.street){
            this.codeData.push(item);
          }
        })
      }else{
        this.codeData = this.streetData;
      }
      this.clientzipcode = this.codeData[0].postcode;
      this.queryId();
    },
    queryId(){
      if(!this.clientinfo.area){
        this.clientinfo.area = this.area
        this.areaData.map((code) => {
          if(code.area === this.clientinfo.area){
            this.$set(this.clientinfo,'frieight',code.frieight);
          }
        })
      }else{
        this.clientinfo.clientzipcode = this.clientzipcode;
        this.clientinfo.clientaddress = this.clientinfo.store_delivery;
        this.codeData.map((code) => {
          if(code.postcode === this.clientzipcode){
            this.$set(this.clientinfo,'frieight',code.frieight);
          }
        })
      }
    },
    address_btn() {
      if (!this.clientinfo.clientname) {
        this.$toast.bottom("...");
        return;
      } else if (!this.clientinfo.clientphone) {
        this.$toast.bottom("...");
        return;
      } else if (!this.clientinfo.city ||!this.clientinfo.area) {
        this.$toast.bottom("...");
        return;
      }
      if(this.clientinfo.store_delivery === '711' || this.clientinfo.store_delivery === ''){
        if(!this.clientinfo.clientzipcode|| this.clientinfo.clientzipcode===',' ||  this.clientinfo.clientzipcode==='' ){
          this.$toast.bottom("...");
          return;
        }
      }
      this.$set(this.clientinfo, "money", this.money);
      this.editClient = false
      localStorage.setItem(this.money+'_clientinfo',JSON.stringify(this.clientinfo));
      this.$emit("closeAddress");
      this.$emit("clientinfoList");
    },
    def() {
      this.area = this.clientinfo.default_client? this.clientinfo.default_client.area: {};
      this.city = this.clientinfo.default_client? this.clientinfo.default_client.city: {};
      this.store = this.clientinfo.default_client? this.clientinfo.default_client.store: {};
      //        this.areaData.push(this.area)
      //        this.storeData.push(this.store)
    }
  },
  computed: {
    ...mapState(["siteInfo", "language"]),
    lang() {
      return function (str) {
        return this.language ? this.language[str] : "";
      };
    }
  }
};
</script>

<style scoped>
</style>
