<template>
  <div class="address" :style="{'height': height}">
    <header-top>
      <img
        src="../../assets/image/icon-close2.png"
        slot="menu"
        height="28"
        @click="$emit('closeAddress')"
      >
      <div class="title" slot="title">{{lang('')}}</div>
    </header-top>
    <div class="address_wrap">
      <component
        v-bind:is="view_name"
        :clientinfo.sync="clientinfo"
        @closeAddress="$emit('closeAddress')"
        @clientinfoList="$emit('clientinfoList')"
      ></component>
    </div>
  </div>
</template>

<script>
import HK from "../../components/commen/address/HK.vue";
import NT from "../../components/commen/address/NT.vue";
import RM from "../../components/commen/address/RM.vue";
import Rp from "../../components/commen/address/Rp.vue";
import SG from "../../components/commen/address/SG.vue";
import RMB from "../../components/commen/address/RMB.vue";
import TZ from "../../components/commen/address/TZ.vue";
import YN from "../../components/commen/address/YN.vue";
import RB from "../../components/commen/address/RB.vue";
import { mapState } from "vuex";

export default {
  props: {
    clientinfo: {
      type: Object
    }
  },
  data() {
    return {
      view_name: '',
      //      view_name:'RM',
      height: window.innerHeight + "px"
    };
  },
  components: {
    HK,
    NT,
    RM,
    Rp,
    RMB,
    TZ,
    SG,
    YN,
    RB
  },
  created() {},
  mounted() {
    if (this.siteInfo.money == "") {
      this.view_name = "RMB";
    } else if (this.siteInfo.money == "") {
      this.view_name = "TZ";
    } else if (this.siteInfo.money == "") {
      this.view_name = "RB";
    } else if (this.siteInfo.money == "") {
      this.view_name = "YN";
    } else if (this.siteInfo.money == "S$") {
      this.view_name = "SG";
    }else{
      this.view_name = this.siteInfo.money;
    }
  },
  computed: {
    ...mapState(["language","siteInfo"]),
    lang() {
      return function(src) {
        return this.language ? this.language[src] : "";
      };
    }
  }
};
</script>

<style scoped lang="stylus">
  .address
    background #fff
  .address_wrap
    padding: 0 4%;
    height: calc(100% - 56px);
    overflow-y: auto;
    background-color: #fff
  #address_wrap >>> .mdui-list-item:hover
    background-color #fff !important
  #address_wrap >>> .mdui-radio input[type=radio]:focus:checked:not(:disabled)+.mdui-radio-icon, .mdui-radio:active input[type=radio]:checked:not(:disabled)+.mdui-radio-icon
    box-shadow:none
  #address_wrap >>>.address-content
     .mdui-list
       .mdui-list-item
         padding: 0;
         font-size: 14px;
         color: #363636;
         .input-infor
           width 95%
           label
             width: 80px;
             padding-left: 25px;
           .necessary
             width: 100%;
           .necessary_address
             width: 100%;
             padding-left: 64px;
           #street
             margin-left: 62px;
             width 25%
           #store
             width 50%
           select
            width 30%
            margin-right 5px
            border none
            border-bottom 1px solid #ccc
            height 25px
            background #fff
           .mag-left
            margin-left 65px
         label
           width: 70px;
           min-width: 60px;
         input
           text-align: left;
           flex: 1;
           outline: 0;
           background: #FFF;
           height: 40px;
           border: none;
           font-size: 14px;
           padding: 1px 0px;
         .city-picker-input
           opacity: 0 !important;
           top: -9999px;
           left: -9999px;
           position: absolute;
         .mdui-list-address
           .city-picker-span
             height: 42px;
             line-height: 42px;
             position: relative;
             display: block;
             outline: 0;
             -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
             border-bottom: 1px solid #ccc;
             background-color: #fff;
             color: #ccc;
             cursor: pointer;
             &>.placeholder
               color: #D1D1D1;
             &>.arrow
               position: absolute;
               top: 50%;
               right: 8px;
               width: 10px;
               margin-top: -3px;
               height: 5px;
               background: url(../../assets/image/drop-arrow.png) -10px -25px no-repeat;
         .city-picker-dropdown
           min-width: auto;
           max-width: 79vw;
           position: absolute;
           width: 315px;
           left: -9999px;
           top: -9999px;
           outline: 0;
           -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
           z-index: 999999;
           display: none;
           margin-bottom: 20px;
           .city-select-wrap
             box-shadow: 0 1px 5px rgba(0, 0, 0, 0.5);
             .city-select-tab
               border-bottom: 1px solid #ccc;
               background: #f0f0f0;
               font-size: 13px;
               a
                 display: inline-block;
                 padding: 8px 22px;
                 border-left: 1px solid #ccc;
                 border-bottom: 1px solid transparent;
                 color: #4D4D4D;
                 text-align: center;
                 outline: 0;
                 text-decoration: none;
                 cursor: pointer;
                 font-size: 14px;
                 margin-bottom: -1px;
                 &.active
                   background: #fff;
                   border-bottom: 1px solid #fff;
                   color: #46A4FF;
               &>a:first-child
                 border-left: none;
             .city-select-content
               width: 100%;
               min-height: 10px;
               background-color: #fff;
               padding: 10px 15px;
               box-sizing: border-box;
               .city-select
                 font-size: 13px
                 dl
                   line-height: 2;
                   clear: both;
                   padding: 3px 0;
                   margin: 0;
                   dt
                     position: absolute;
                     width: 2.5em;
                     font-weight: 500;
                     text-align: right;
                     line-height: 2;
                   dd
                     margin-left: 3em;
                     line-height: 2;
                     a
                       display: inline-block;
                       padding: 0 10px;
                       outline: 0;
                       text-decoration: none;
                       white-space: nowrap;
                       margin-right: 2px;
                       color: #333;
                       cursor: pointer;
  #address_wrap >>>.saveAddress
   margin-top: 10px;
   height: 40px;
   border: none;
   width: 100%;
   background: #B4272D;
   border-radius: 4px;
   font-size: 16px;
   color: #FFFFFF;
   text-align: center;
   line-height: 16px;

</style>
