/* eslint-disable camelcase,semi,key-spacing,keyword-spacing */
import http from './http'
var Address_List = [];

export default {
  load: async (money) => {
    if (money === 'S$' || money === '') return;
    let url = '/json/' + money + '.json';
    const data = await http.load(url);
    Address_List = data;
    return data;
  },
  get: (money, city, area, addressId) => {
    let address_list = [];
    if (addressId === 'city') {
      if (Address_List && Address_List.length > 0) {
        if (money === '') {
          address_list.push({city: `/ Province`});
          for (let info of Address_List) {
            address_list.push({city: info.city});
          }
        }
        if (money === 'RM') {
          address_list.push({city: `Province`});
          for (let info of Address_List) {
            address_list.push({city: info.city});
          }
        }
        if (money === 'NT') {
          address_list.push({city: ``});
          for (let info of Address_List) {
            address_list.push({city: info.city});
          }
        }
        if (money === 'HK') {
          address_list.push({city: ``});
          for (let info of Address_List) {
            address_list.push({city: info.city});
          }
        }
        if (money === 'Rp') {
          address_list.push({city: `Province`});
          for (let info of Address_List) {
            address_list.push({city: info.city});
          }
        }
        if (money === '') {
          address_list.push({city: `Province`});
          for (let info of Address_List) {
            address_list.push({city: info.city});
          }
        }
        if (money === '') {
          for (let info of Address_List) {
            address_list.push({city: info.city});
          }
        }
      }
    } else if (addressId === 'area') {
      if (Address_List && Address_List.length > 0) {
        if (money === '') {
          address_list.push({area: ''});
          Address_List.map((info) => {
            if (info.city === city) {
              address_list = address_list.concat(info.area);
            }
          })
        } else if (money === 'RM') {
          address_list.push({area: 'City'});
          Address_List.map((info) => {
            if (info.city === city) {
              address_list = address_list.concat(info.area);
            }
          })
        } else if (money === 'NT') {
          address_list.push({area: ''});
          Address_List.map((info) => {
            if (info.city === city) {
              address_list = address_list.concat(info.area);
            }
          })
        } else if (money === 'HK') {
          address_list.push({area: ''});
          Address_List.map((info) => {
            if (info.city === city) {
              address_list = address_list.concat(info.area);
            }
          })
        } else if (money === 'Rp') {
          address_list.push({area: 'City'});
          Address_List.map((info) => {
            if (info.city === city) {
              address_list = address_list.concat(info.area);
            }
          })
        } else if (money === '') {
          address_list.push({area: 'City'});
          Address_List.map((info) => {
            if (info.city === city) {
              address_list = address_list.concat(info.area);
            }
          })
        }
      }
    } else if (addressId === 'code') {
      if (money === 'RM' || money === ''||money==='NT') {
        if (Address_List && Address_List.length > 0) {
          if(money!=='NT'){
            address_list.push({postcode: ' '});
          }
        }
        for (let i in Address_List) {
          if( Address_List[i].city === city){
            for (let j in Address_List[i].area) {
              if (Address_List[i].area[j].area === area) {
                for (let k in Address_List[i].area[j].postcode) {
                  address_list.push(Address_List[i].area[j].postcode[k]);
                }
              }
            }
          }
        }
      }
    }
    return address_list;
  }
}
